import { memo, useRef } from "react";
import {
  Container,
  DFC,
  PlayList,
  TrackSubTitle,
  TrackTitle,
  TrackWrap,
  TrackImgWrap,
  TrackImg,
  Plug,
  PlugDot
} from "../styled-components";

function Playlist({ data, handleTrack, stopTrack, pauseTrack, next, setNext }) {
  const onPlay = (e) => {
    data.tracklist.forEach((item) => {
      if (+item.id === +e.currentTarget.id && item.plays === false) {
        handleTrack(e.currentTarget.id);
      } else if (+item.id !== +e.currentTarget.id && item.plays === true) {
        stopTrack(item.id);
      }
    });
  };
  const cleck = (e) => {
    if (e.target.tagName === "A") {
      return;
    }
    data.tracklist.forEach((item, index) => {
      if (
        +item.id === +e.currentTarget.id &&
        item.plays === false &&
        item.pause === true
      ) {
        onPlay(e);
        pauseTrack(e.currentTarget.id);
        setNext(index);
      } else if (+item.id === +e.currentTarget.id && item.pause === false) {
        pauseTrack(e.currentTarget.id);
      } else if (
        +item.id === +e.currentTarget.id &&
        item.pause === true &&
        item.plays === true
      ) {
        pauseTrack(item.id);
      }
    });
  };
  let style = {
    animation: ""
  };
  let list = data.tracklist.map((item, index) => (
    <TrackWrap key={index} id={item.id} onClickCapture={cleck}>
      <TrackImgWrap>
        <TrackImg src={item.image.background} alt={item.title} />
        {item.plays === true ? (
          <Plug>
            <PlugDot
              style={
                item.plays === true && item.pause === false
                  ? { animation: "plug .8s infinite ease-in-out alternate" }
                  : { animation: "none" }
              }
            ></PlugDot>
          </Plug>
        ) : (
          ""
        )}
      </TrackImgWrap>
      <DFC>
        <TrackTitle>{item.title}</TrackTitle>
        <TrackSubTitle>{item.subtitle}</TrackSubTitle>
      </DFC>
      <a id="download" download href={item.src}>
        Загрузить
      </a>
    </TrackWrap>
  ));
  return (
    <Container>
      <PlayList>{list}</PlayList>
    </Container>
  );
}
export default memo(Playlist);
