import initialState from "./initialState";

export default function AudioReducer(state = initialState.trackList, action) {
  switch (action.type) {
    case "ADD_TRACK": {
      return [
        ...state,
        {
          id: action.payload.id,
          src: action.payload.src,
          image: {
            background: action.payload.image.background,
            coverart: action.payload.image.coverart,
            coverarthq: action.payload.image.coverarthq
          },
          title: action.payload.title,
          subtitle: action.payload.subtitle,
          url: action.payload.url,
          plays: false,
          pause: true,
          isNext: false
        }
      ];
    }
    case "DELETE_TRACK": {
      let id = action.payload.id;
      return [...state.filter((item) => +item.id !== +id)];
    }
    case "HANDLE_TRACK": {
      let id = action.payload.id;
      return [
        ...state.map((item) => {
          if (+item.id === +id) {
            item.plays = !item.plays;
            item.isNext = false;
            return item;
          } else {
            item.isNext = true;
            return item;
          }
        })
      ];
    }
    case "STOP_TRACK": {
      let id = action.payload.id;
      return [
        ...state.map((item) => {
          if (+item.id === +id) {
            item.plays = false;
            item.pause = true;
            return item;
          }
          return item;
        })
      ];
    }
    case "PAUSE_TRACK": {
      let id = action.payload.id;
      return [
        ...state.map((item) => {
          if (+item.id === +id) {
            item.pause = !item.pause;
            return item;
          }
          return item;
        })
      ];
    }
    default: {
      return state;
    }
  }
}
