import { memo, useEffect, useRef, useState } from "react";
import AudioPlayer from "react-h5-audio-player";
import {
  TrackImgWrap,
  TrackImg,
  Playerlist,
  PlayerlistWrap,
  DFC
} from "../styled-components";
import "react-h5-audio-player/lib/styles.css";
function PlayerList({
  data,
  handleTrack,
  stopTrack,
  pauseTrack,
  changeVolume,
  next,
  setNext
}) {
  const cur = useRef();
  const onPause = (e) => {
    data.tracklist.forEach((item) => {
      let elem = cur.current.id;
      if (item.pause === false && +item.id === +elem.slice(0, -4)) {
        pauseTrack(item.id);
      }
    });
  };
  const onPlay = (e) => {
    if (data) {
      data.tracklist.forEach((item) => {
        if (e) {
          let elem = cur.current.id;
          if (
            +item.id === +elem.slice(0, -4) &&
            item.pause === true &&
            item.plays === true
          ) {
            pauseTrack(item.id);
          }
        }
      });
    }
  };
  const end = (e) => {
    let id = cur.current.id.slice(0, -4);
    stopTrack(id);
    // data.tracklist.map((item, index) => {
    //   if (+item.id === +id){
    //     let id2 = data.tracklist[index+1].id;
    //     handleTrack(id2);
    //     setTimeout(()=>{
    //       let el =
    //     })
    //   }
    // });
    onClickNext();
  };
  const pl = (e) => {
    if (data) {
      data.tracklist.map((item, index) => {
        if (item.plays === true && item.pause === false) {
          let elem = cur.current;
          if (elem) {
            elem.childNodes.forEach((child) => {
              if (child.classList.contains("rhap_container")) {
                child.childNodes.forEach((ch) => {
                  if (ch.tagName === "AUDIO") {
                    ch.play();
                  }
                });
              }
            });
          }
        } else if (item.pause === true && item.plays === true) {
          let elem = cur.current;
          if (elem) {
            elem.childNodes.forEach((child) => {
              if (child.classList.contains("rhap_container")) {
                child.childNodes.forEach((ch) => {
                  if (ch.tagName === "AUDIO") {
                    ch.pause();
                  }
                });
              }
            });
          }
        }
      });
    }
  };
  useEffect(() => {
    pl();
  });
  const onClickNext = (e) => {
    if (next < data.tracklist.length - 1) {
      setNext(next + 1);
      let id1 = data.tracklist[next].id;
      let id2 = data.tracklist[next + 1].id;
      handleTrack(id2);
      stopTrack(id1);
      setTimeout(() => {
        let el = document.getElementsByTagName("audio");
        el[0].play();
      }, 30);
    } else {
      setNext(0);
      let id1 = data.tracklist[next].id;
      let id2 = data.tracklist[0].id;
      handleTrack(id2);
      stopTrack(id1);
      setTimeout(() => {
        let el = document.getElementsByTagName("audio");
        el[0].play();
      }, 30);
    }
  };
  const onClickPrevious = (e) => {
    if (next > 0) {
      setNext(next - 1);
      let id1 = data.tracklist[next].id;
      let id2 = data.tracklist[next - 1].id;
      handleTrack(id2);
      stopTrack(id1);
      setTimeout(() => {
        let el = document.getElementsByTagName("audio");
        el[0].play();
      }, 30);
    } else {
      setNext(data.tracklist.length - 1);
      let id1 = data.tracklist[next].id;
      let id2 = data.tracklist[data.tracklist.length - 1].id;
      handleTrack(id2);
      stopTrack(id1);
      setTimeout(() => {
        let el = document.getElementsByTagName("audio");
        el[0].play();
      }, 30);
    }
  };
  const onVolumeChange = (e) => {
    let volume = e.target.volume;
    changeVolume(volume);
  };
  let list = data.tracklist.map((item, index) =>
    item.plays === true ? (
      <PlayerlistWrap id={item.id + "-top"} key={index} ref={cur}>
        <TrackImgWrap>
          <TrackImg src={item.image.background} />
        </TrackImgWrap>
        <DFC>
          <div>{item.title}</div>
          <div>{item.subtitle}</div>
        </DFC>
        <AudioPlayer
          src={item.src}
          index={index}
          volume={data.ui[0].volume}
          showSkipControls={true}
          showFilledVolume={true}
          onPlay={onPlay}
          onPause={onPause}
          onEnded={end}
          onClickNext={onClickNext}
          onClickPrevious={onClickPrevious}
          onVolumeChange={onVolumeChange}
          style={{ flexBasis: "50%" }}
        />
      </PlayerlistWrap>
    ) : (
      ""
    )
  );
  return <Playerlist>{list}</Playerlist>;
}
export default memo(PlayerList);
