import { useState } from "react";
import { connect } from "react-redux";
import {
  addTrack,
  deleteTrack,
  handleTrack,
  stopTrack,
  pauseTrack,
  changeVolume
} from "../actions";
import PlayerList from "../components/PlayerList";
import PlayList from "../components/PlayList";
function Player(props) {
  const [next, setNext] = useState(0);
  const data = () => {
    return props.state;
  };
  const addTrack = (e) => {
    //console.log(e);
  };
  const deleteTrack = (e) => {
    //console.log(e);
  };
  const handleTrack = (id) => {
    props.handleTrack(id);
  };
  const stopTrack = (id) => {
    props.stopTrack(id);
  };
  const pauseTrack = (id) => {
    props.pauseTrack(id);
  };
  const changeVolume = (volume) => {
    props.changeVolume(volume);
  };
  return (
    <div>
      <h1>PlayList</h1>
      <PlayerList
        data={data()}
        handleTrack={handleTrack}
        stopTrack={stopTrack}
        pauseTrack={pauseTrack}
        changeVolume={changeVolume}
        next={next}
        setNext={setNext}
      />
      <PlayList
        data={data()}
        handleTrack={handleTrack}
        stopTrack={stopTrack}
        pauseTrack={pauseTrack}
        next={next}
        setNext={setNext}
      />
    </div>
  );
}
function mapStateToProps(state) {
  return { state };
}
function mapDispatchToProps(dispatch) {
  return {
    addTrack: (
      id,
      src,
      background,
      coverart,
      coverarthq,
      title,
      subtitle,
      url
    ) => {
      dispatch(
        addTrack(
          id,
          src,
          background,
          coverart,
          coverarthq,
          title,
          subtitle,
          url
        )
      );
    },
    deleteTrack: (id) => {
      dispatch(deleteTrack(id));
    },
    handleTrack: (id) => {
      dispatch(handleTrack(id));
    },
    stopTrack: (id) => {
      dispatch(stopTrack(id));
    },
    pauseTrack: (id) => {
      dispatch(pauseTrack(id));
    },
    changeVolume: (volume) => {
      dispatch(changeVolume(volume));
    }
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Player);
