export const addTrack = (
  id,
  src,
  background,
  coverart,
  coverarthq,
  title,
  subtitle,
  url
) => {
  return {
    type: "ADD_TRACK",
    payload: {
      id,
      src,
      image: {
        background,
        coverart,
        coverarthq
      },
      title,
      subtitle,
      url
    }
  };
};
export const deleteTrack = (id) => {
  return {
    type: "DELETE_TRACK",
    payload: {
      id
    }
  };
};
export const handleTrack = (id) => {
  return {
    type: "HANDLE_TRACK",
    payload: {
      id
    }
  };
};
export const stopTrack = (id) => {
  return {
    type: "STOP_TRACK",
    payload: {
      id
    }
  };
};
export const pauseTrack = (id) => {
  return {
    type: "PAUSE_TRACK",
    payload: {
      id
    }
  };
};
export const changeVolume = (volume) => {
  return {
    type: "CHANGE_VOLUME",
    payload: {
      volume
    }
  };
};
