import styled from "styled-components";
export const Container = styled("div")`
  max-width: 1200px;
  margin: 0 auto;
`;
export const DF = styled("div")`
  display: flex;
`;
export const DFC = styled(DF)`
  flex-direction: column;
`;
export const DFR = styled(DF)`
  flex-direction: row;
`;
export const PlayList = styled(DFC)``;
export const TrackWrap = styled(DFR)`
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
  position: relative;
  cursor: pointer;
  :last-child {
    margin-bottom: 0;
  }
`;
export const TrackTitle = styled("div")`
  font-size: 25px;
  color: #000;
  font-weight: bold;
`;
export const TrackSubTitle = styled(TrackTitle)`
  font-size: 20px;
  font-weight: normal;
`;
export const TrackImgWrap = styled("div")`
  width: 100px;
  height: 100px;
  position: relative;
`;
export const TrackImg = styled("img")`
  width: 100%;
  height: 100%;
`;
export const Playerlist = styled(Container)`
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
`;
export const PlayerlistWrap = styled(DFR)`
  align-items: center;
  justify-content: space-between;
  background-color: aliceblue;
  padding: 10px 25px;
`;
export const Plug = styled("div")`
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  top: 0;
`;
export const PlugDot = styled("div")`
  width: 30px;
  height: 30px;
  border: 1px solid #e7de09;
  border-radius: 50%;
  position: absolute;
  top: 35%;
  left: 35%;
  background-color: #e7de09;
`;
