import initialState from "./initialState";

export default function UiReducer(state = initialState.ui, action) {
  switch (action.type) {
    case "CHANGE_VOLUME": {
      let vol = action.payload.volume;
      return [
        ...state.map((item) => {
          item.volume = vol;
          return item;
        })
      ];
    }
    default: {
      return state;
    }
  }
}
