import { createRoot } from "react-dom/client";
import { Provider } from "react-redux";
import configStore from "./store/store";
import App from "./App";
import axios from "axios";
import { addTrack } from "./actions";

const rootElement = document.getElementById("root");
const root = createRoot(rootElement);
const store = configStore();
//const u = store.subscribe(() => console.log(store.getState()));
const options = {
  method: "GET",
  url: "https://shazam.p.rapidapi.com/search",
  params: { term: "country", locale: "en-US", offset: "0", limit: "5" },
  headers: {
    "X-RapidAPI-Key": "e9798ae78fmshe7ce65d0ebfcd44p1e4373jsn2181fed414ed",
    "X-RapidAPI-Host": "shazam.p.rapidapi.com"
  }
};

axios
  .request(options)
  .then(function (res) {
    res.data.tracks.hits.forEach((item) => {
      store.dispatch(
        addTrack(
          item.track.key,
          item.track.hub.actions[1].uri,
          item.track.images.background,
          item.track.images.coverart,
          item.track.images.coverarthq,
          item.track.title,
          item.track.subtitle,
          item.track.url
        )
      );
    });
  })
  .catch(function (error) {
    console.error(error);
  });
root.render(
  <Provider store={store}>
    <App />
  </Provider>
);
